.signin {
  width: calc(100% - 20px);
  max-width: 340px;
  box-sizing: border-box;
  background: #ffffff;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);
  border-radius: 12px;
  margin: 20px auto;
  padding: 0 30px;
  color: #1e1e1e;
}
